import React, {useState, useEffect, Fragment} from 'react';
import {
    Gallery,
    Header,
    Placeholder,
    Banner,
    Panel,
    PanelHeader,
    Card,
    CardGrid,
    PanelHeaderBack,
    Button,
    MiniInfoCell,
    Caption,
    Subhead,
    TabsItem,
    Tabs,
    Div,
    ActionSheet,
    ActionSheetItem,
    Avatar,
    Input,
    RichCell,
    Footer, Separator, Counter, Group, Cell, Select
} from "@vkontakte/vkui";
import $ from 'jquery'
import './Home.css'
import bridge from '@vkontakte/vk-bridge';
import fetchJsonp from "fetch-jsonp"
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon20WorkOutline from '@vkontakte/icons/dist/20/work_outline';
import Icon56ArticleOutline from '@vkontakte/icons/dist/56/article_outline';
import Delivery from './components/Delivery'
import CartListItems from "./components/CartListItems";

const axios = require('axios');
const jsonpAdapter = require('axios-jsonp');

const Cart = ({ id, go, fetchedUser,activeCity, setArrCities, onDecrementPosition, onIncrementPosition, order, clearCart, onDeletePosition, setPopout }) => {
    const [sum, setSum] = useState(null);
    const [count, setCount] = useState(null);
    const [sumDeliv, setSumDeliv] = useState(null);
    const [weight, setWeight] = useState(null);
    const [postal, setPostal] = useState(null);
    const [qCity, setQCity] = useState(null);
    const [deliv, setDeliv] = useState(null);
    const [typeDelivery, setTypeDelivery] = useState(null);

    useEffect(() => {
        console.log('Изменились рассчетные данные')
        if (activeCity !== null && typeDelivery!== null) getRange(activeCity.id, typeDelivery)
    }, [activeCity, typeDelivery, order]);

    useEffect(()=>{
        getOrder(order)
        console.log('Заказ изменен ',order)
    }, [order]);


    useEffect(() => {
        fetch('http://localhost:3030/items/getCities')
            .then(res => res.json())
            .then(res=> {
                console.log(res)
                setArrCities(res)
            })
    }, []);

    const getOrder = (order) => {
        let count = 0;
        let sum = 0;
        let weight = 0;
        for (let key in order){
            count = count + order[key].count;
            sum = sum + order[key].count * order[key].item.price
            weight = weight + order[key].count * order[key].item.weight
        }
        setCount(count);
        setSum(sum);
        setWeight(weight);
    };

    const getRange = () => {
        //sahpossum.herokuapp.com
        //localhost:3000
        fetch('https://sahpossum.herokuapp.com/items/getRange', {
            method: 'POST', // *GET, POST, PUT, DELETE, etc.
            mode: 'cors', // no-cors, cors, *same-origin
            cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
            credentials: 'same-origin', // include, *same-origin, omit
            headers: {
                'Content-Type': 'application/json',
                // 'Content-Type': 'application/x-www-form-urlencoded',
            },
            redirect: 'follow', // manual, *follow, error
            referrer: 'no-referrer', // no-referrer, *client
            body: JSON.stringify({
                "cityCode": activeCity.id,
                "weight": String(weight),
                "count": count,
                "typeDelivery": String(typeDelivery)
            }), // тип данны
        })
            .then(res=>res.json())
            .then(res=>{
                setDeliv(res)
            })
            .catch(e=>{
                console.log(e)
            })
    }

    const pay = (fetchedUser) => {
        if (fetchedUser.id){
            bridge.send("VKWebAppOpenPayForm", {"app_id": 7600142, "action": "pay-to-user", "params": {
                    // "data": {},
                    "amount": 1,
                    "user_id": 199500866,
                    // "group_id": 199500866,
                    "description": `Оплата товаров на сумму ${sum} в магазине (в рамках тестирования сумма 1 рубль)`
                },
            });
        }
    }

    if (count === 0){
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
                <Placeholder
                    stretched
                    icon={<Icon56ArticleOutline />}
                    header={'Ваша корзина пуста'}
                    action={<Button onClick={()=>go('home')} mode={'outline'} size="l">Продолжить покупки</Button>}
                />
                <Footer/>
            </Panel>
        )
    } else {
        return (
            <Panel id={id}>
                <PanelHeader left={<PanelHeaderBack onClick={()=>go('home')} />}/>
                <CartListItems
                    order={order}
                    onDeletePosition={onDeletePosition}
                    onDecrementPosition={onDecrementPosition}
                    onIncrementPosition={onIncrementPosition}
                    clearCart={clearCart}
                />
                <Fragment>
                    <Div>
                        <MiniInfoCell after={<Counter>{count}</Counter>}>Товаров (штуки)</MiniInfoCell>
                        <MiniInfoCell after={<Counter>{sum}</Counter>}>Сумма (рубли)</MiniInfoCell>
                    </Div>
                    <Group header={<Header mode="secondary">Доставка</Header>}>
                            <Cell
                                expandable
                                onClick={()=>go('targetCity')}
                                indicator={activeCity !== null ? activeCity.name : 'Не выбрано' }
                            >Город</Cell>
                            {
                                activeCity !== null &&
                                <Div>
                                    <Select onChange={event=>setTypeDelivery(event.target.value)} placeholder="Выберите тип доставки">
                                        <option value="136">До склада</option>
                                        <option value="137">До квартиры</option>
                                    </Select>
                                </Div>
                            }
                        {
                            deliv !== null &&
                                <Div>
                                    <Delivery deliv={deliv} sum={sum} />
                                    {
                                        !deliv.error &&
                                        <Button after={<Counter>{sum+Math.round(Number(deliv.price))}</Counter>} mode={'outline'} size={'xl'}  onClick={()=>pay(fetchedUser)}>Оплатить</Button>
                                    }
                                </Div>
                        }
                    </Group>
                    {/*{*/}
                    {/*    activeCity === null ?*/}
                    {/*        <Div>*/}
                    {/*            <Group header={<Header mode="secondary">Укажите город доставки</Header>}>*/}
                    {/*                <Input value={qCity} onChange={(e)=>setQCity(e.target.value)} />*/}
                    {/*                {*/}
                    {/*                    qCity !== null &&*/}
                    {/*                        <Button mode={'outline'} size={'xl'}  onClick={()=>getAddress()}>Рассчитать доставку</Button>*/}
                    {/*                }*/}

                    {/*            </Group>*/}
                    {/*        </Div> :*/}
                    {/*        <Div>*/}
                    {/*            <MiniInfoCell textWrap={'full'} mode={'more'}>{activeCity.name}</MiniInfoCell>*/}
                    {/*            <Button mode={'outline'} size={'xl'}  onClick={()=>setActiveCity(null)}>Изменить данные доставки</Button>*/}
                    {/*            {deliv || ''}*/}
                    {/*        </Div>*/}
                    {/*}*/}
                    {/*<Div>*/}
                    {/*    <Button mode={'outline'} size={'xl'}  onClick={()=>pay(fetchedUser)}>Оплатить</Button>*/}
                    {/*</Div>*/}
                </Fragment>
                <Footer/>
            </Panel>
        )
    }
};

export default Cart
